import { useState } from "react";
import AppTitle from "../AppTitle";
import LoginField from "./LoginField";
import { Navigate, Redirect, useNavigate } from "react-router-dom";
import "./login.css";
import { useEffect } from "react";
import useFetch from "../useFetch.js";
const Login = ({ users_json }) => {
  const url = (path) => `https://lively-backend30.herokuapp.com${path}`;
  localStorage.setItem("benson", "thomas");
  const [loggedIn, setLoggedIn] = useState(
    JSON.parse(localStorage.getItem("loggedIn"))
  );
  const [uExists, setUExists] = useState(null);
  const navigate = useNavigate();
  const [errorMessage, setErrorMessage] = useState("");
  const [loginError, setLoginError] = useState(false);

  // const {
  //   error,
  //   isPending,
  //   data: users_json,
  // } = useFetch("https://jsonplaceholder.typicode.com/users");

  // localStorage.setItem("users", JSON.stringify(users_json));

  // localStorage.setItem("length", users_json.length);

  const users = JSON.parse(localStorage.getItem("all_users"));
  // localStorage.setItem("length", users.length);

  const { perror, pisPending, data } = useFetch(
    "https://jsonplaceholder.typicode.com/posts"
  );

  const handleSubmit = (e, uname, pwd) => {
    e.preventDefault();
    setLoginError(false);
    let username = uname;
    let password = pwd;
    let userDetails = {
      username: uname,
      password: pwd,
    };
    fetch(url("/login"), {
      method: "POST",
      credentials: "include",
      withCredentials: true,
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
      body: JSON.stringify(userDetails),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        localStorage.setItem("cookie", JSON.stringify(res.cookie));
        localStorage.setItem("currUser", JSON.stringify(res));
        if (res.result === "success") {
          navigate("/home");
        } else {
          setErrorMessage(res.result);
          setLoginError(true);
        }
      });
  };

  const storeValues = (currUser) => {
    localStorage.setItem("users", JSON.stringify(users));
    localStorage.setItem("currUser", JSON.stringify(currUser));
    localStorage.setItem(
      "cu_status",
      JSON.stringify(currUser["company"]["catchPhrase"])
    );
    // setMyPosts(currUser);
  };
  const handleSignUp = () => {
    navigate("/register");
  };

  return (
    <div className="loginLayout">
      <div className="loginLeft">
        <div className="loginLeftContent">
          <h1>Login to Your Account</h1>
          <LoginField handleSubmit={handleSubmit} />
          {loginError && <span className="redText">{errorMessage}</span>}
        </div>
      </div>

      <div className="loginRight">
        <div className="loginRightContent">
          <h2>New here?</h2>
          <p>Sign up and make your life more lively!</p>
          <button className="signUpBtn" onClick={() => handleSignUp()}>
            SignUp
          </button>
        </div>
      </div>
    </div>
  );
};

export default Login;
